import dividLine from '@/components/line.vue';
import scrollIndex from '@/components/scroll-index.vue';
import mediaList from '@/components/mediaList.vue'
import goodList from '@/components/good-list.vue'
import uniLoadMore from '@/components/uni-load-more.vue';
import goTop from '@/components/go-top.vue';
import uniIcon from '@/components/uni-icon.vue';
import uniGrid from '@/components/uni-grid.vue';
import {
	getGoodsList,
	getItemList
} from '@/api/goods.js'
export default {
	components: {
		dividLine,
		scrollIndex,
		mediaList,
		goodList,
		uniLoadMore,
		goTop,
		uniIcon,
		uniGrid
	},
	data() {
		return {
			isDefault: true, //默认排序
			isTotal_sales: 0, //销量排序
			isTk_rate: 0, //佣金排序
			isPrice: 0, //价格排序
			sortTabFixed: false,

			scrollLeft: 0,
			isClickChange: false,
			tabIndex: 0,
			toTop: false,
			page: 1,
			newsitems: [],
			loadingType: 1,
			loadingTitle: "正在玩命加载...",
			mask: true,
			//条件筛选
			active: 0,
			isup: 3,
			sorts: new Object(),
			isDefault: true, //默认排序
			isTotal_sales: 0, //销量排序
			isTk_rate: 0, //佣金排序
			isPrice: 0, //价格排序
			sortTabFixed: false,
			pages: new Array(),
			type: "",
			option: new Object()
		}
	},
	onLoad(option) {
		this.type = option.type;
		uni.setNavigationBarTitle({
			title: this.type
		})
		// uni.showLoading({
		// 	title: "正在玩命加载..."
		// })
		//初始化数据
		console.log(this.randomfn());
		// setTimeout(function() {
		// 	uni.hideLoading()
		// }, 2000)
	},
	onNavigationBarButtonTap(e) {
		console.log(e);
		if (e.index == 0) {
			uni.navigateTo({
				url: '/pages/index/board'
			})
		}
	},
	onNavigationBarSearchInputClicked() {
		uni.switchTab({
			url: '/pages/common/search'
		})
	},
	methods: {
		scroll(e) {},
		touchmove(e) {
			console.log(uni.getStorageSync("nextPage" + this.type));
			if (!uni.getStorageSync("nextPage" + this.type)) {
				this.page++;
				console.log(this.page);
				getGoodsList({
					page: this.page,
					type: this.type
				}).then(res => {
					this.loadingType = 2;
					//this.newsitems[index].data = this.newsitems[index].data.concat(res.result);
					uni.setStorageSync("nextPage" + this.type, res.result);

					if (res.result < 10) {
						this.loadingType = 2;
						return;
					}
				})
			}
		},
		loadMore(e) {
			setTimeout(() => {
				// console.log(this.newsitems);
				console.log(this.newsitems.concat(uni.getStorageSync("nextPage" + this.type)));
				this.newsitems = this.newsitems.concat(uni.getStorageSync("nextPage" + this.type));
				uni.removeStorageSync("nextPage" + this.type);
			}, 300);
		},
		randomfn() {
			let type = this.type;
			console.log(type);
			if (type == '聚划算' || type == '淘抢购' || type == '9.9元') {
				type = 'special'
			}
			let a = 0;
			switch (type) {
				case 'special':
					for(let i=0;i<3;i++){
						console.log(this.newsitems.length);
						getItemList({
							page: this.page,
							type: this.type
						}).then(res => {
							console.log(res);
							this.loadingType = 2;
							this.newsitems = res.data.result;
							if(this.newsitems.length>11){
								
							}
						});
						break
					}
					break;
				default:
					getGoodsList({
						page: this.page,
						type: this.type
					}).then(res => {
						console.log(res);
						this.loadingType = 2;
						this.newsitems = res.result;
						return res.data.result;
					});
			}
			return type;
		},
		screentap(sortType) {
			let type = this.type;
			console.log(this.type);
			if (sortType == '') {
				this.isDefault = true;
				this.isTotal_sales = 0;
				this.isTk_rate = 0;
				this.isPrice = 0;
				this._getGoodsList({
					type: type,
					sorts: sortType
				});
			} else if (sortType == 'total_sales') {
				this.isDefault = false;
				this.isTk_rate = 0;
				this.isPrice = 0;
				if (this.isTotal_sales == 0 || this.isTotal_sales == 1) {
					this._getGoodsList({
						sorts: sortType + '_des',
						type: type
					});
					this.isTotal_sales = 2;
				} else if (this.isTotal_sales == 2) {
					this.sorts.sort1 = sortType + '_asc';
					this._getGoodsList({
						sorts: sortType + '_asc',
						type: type
					});
					this.isTotal_sales = 1;
				}
			} else if (sortType == 'tk_rate') {
				this.isDefault = false;
				this.isTotal_sales = 0;
				this.isPrice = 0;
				if (this.isTk_rate == 0 || this.isTk_rate == 1) {
					this._getGoodsList({
						sorts: sortType + '_des',
						type: type
					});
					this.isTk_rate = 2;
				} else {
					this._getGoodsList({
						sorts: sortType + '_asc',
						type: type
					});
					this.isTk_rate = 1;
				}
			} else if (sortType == 'price') {
				this.isDefault = false;
				this.isTotal_sales = 0;
				this.isTk_rate = 0;
				if (this.isPrice == 0 || this.isPrice == 1) {
					this._getGoodsList({
						sorts: sortType + '_des',
						type: type
					});
					this.isPrice = 2;
				} else {
					this._getGoodsList({
						sorts: sortType + '_asc',
						type: type
					});
					this.isPrice = 1;
				}
			}
		},
		_getGoodsList(obj) {
			console.log(obj);
			uni.showLoading({
				title: '加载中..'
			})
			let ret = getGoodsList({
				type: obj.type,
				sorts: obj.sorts
			});
			ret.then(res => {
				uni.hideLoading();
				if (res.code == 200) {
					this.newsitems = res.result
				} else {
					console.log("请求失败");
				}
			})
		}
	}
}
